const CryptoJS = require('crypto-js') // 引用AES源码js
import store from '@/store'

var recKey = sessionStorage.getItem('secretKey')
var ivData = sessionStorage.getItem('iv')

// const recKey = store.getters.secretKey;
// const ivData = store.getters.iv;
const key = CryptoJS.enc.Utf8.parse(recKey) // 十六位十六进制数作为秘钥
const iv = CryptoJS.enc.Utf8.parse(ivData) // 十六位十六进制数作为秘钥偏移量
// const key = CryptoJS.enc.Utf8.parse("1234123412ABCDEF"); //十六位十六进制数作为秘钥
// const iv = CryptoJS.enc.Utf8.parse('ABCDEF1234123412'); //十六位十六进制数作为秘钥偏移量

// 解密方法
function Decrypt(word) {
  // console.log('333311',word)
  // let base64 = CryptoJS.enc.Base64.parse(word);
  const encryptedHexStr = atob(word)
  const encryptedHexStrs = CryptoJS.enc.Hex.parse(encryptedHexStr)
  const srcs = CryptoJS.enc.Base64.stringify(encryptedHexStrs)
  let decrypt = CryptoJS.AES.decrypt(srcs, key, { iv: iv, mode: CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7 })

  const decryptedStr = decrypt.toString(CryptoJS.enc.Utf8)

  return decryptedStr.toString()
}

// 加密方法
function Encrypt(word) {
  const srcs = CryptoJS.enc.Utf8.parse(word)
  const encrypted = CryptoJS.AES.encrypt(srcs, key, { iv: iv, mode: CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7 })
  const encrypting = btoa(encrypted.ciphertext.toString())
  // return encrypted.ciphertext.toString().toUpperCase();
  return encrypting
}

